.web-content-wrap {
  width: 950px;
  display: flex;
  flex-wrap: wrap;
  margin: 60px auto;
  position: relative;
  @media (max-width: 768px) {
    width: 100%;
    padding: 0 20px;
  }
  > div {
    @media (max-width: 768px) {
      width: 100% !important;
      margin: 55px 0 0 !important;
      &:first-child {
        margin: 0 !important;
      }
    }
    width: 40%;
    padding-left: 10%;
    &:first-child,
    &:nth-child(2) {
      margin-bottom: 45px;
    }
    &:nth-child(2n) {
      width: 45%;
      margin-left: 10%;
      padding-right: 5%;
    }
    > div {
      &:first-child {
        @media screen and (max-width: 768px) {
          flex-direction: column;
          align-items: center;
          justify-content: center;
        }

        img {
          @media screen and (max-width: 768px) {
            // margin-right: 0;
            margin-bottom: 20px;
          }
          width: 40px;
          margin: 0 auto 15px;
          display: block;
        }
        span {
          display: block;
          margin: 0 auto 10px;
          text-align: center;
        }
      }
      &:last-child {
        text-align: center;
        @media screen and (max-width: 768px) {
          margin-left: 0;
          text-align: center;
        }
      }
    }
  }
}
